:import("../shared/variable.css") {
  i__const_sliver_70: sliver;
  i__const_mobile_71: mobile;
}

:export {
  sliver: i__const_sliver_70;
  mobile: i__const_mobile_71;
}

.sl {
    font-size: 24px;
}

.slItem {
    border-bottom: 1px solid i__const_sliver_70;
    margin-bottom: 30px;
}

.slItem:last-of-type {
    border-bottom: 0px;
}

.flex {
    display: -ms-flexbox;
    display: flex;
    padding-top: 15px;
    padding-bottom: 15px;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-right: -15px;
    margin-left: -15px;
}

.flex > div {
    margin: 0 15px;
}

.itemImg {
    height: 200px;
    width: 40%;
    background-position: center;
    background-size: cover;
    border-radius: 4px;
}

@media i__const_mobile_71 {
    .itemImg {
        display: none;
    }
}

.itemIntro {
    -ms-flex-item-align: center;
        align-self: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.itemCheckList {
    -ms-flex-positive: 2;
        flex-grow: 2;
}



