:import("../shared/variable.css") {
  i__const_sliver_56: sliver;
}
:export {
  sliver: i__const_sliver_56;
}
.box {
    display: inline-block;
    height: 12px;
    width: 12px;
    border: 1px solid i__const_sliver_56;
    border-radius: 3px;
    margin-right: 10px;
    position: relative;
}
.box:after {
    position: absolute;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
    width: 8px;
    height: 8px;
    margin: 2px;
    border-radius: 2px;
    background-color: #242424;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    opacity: 0;
}
.selectedBox {
    composes: box;
}
.selectedBox:after {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}



