:import("../shared/variable.css") {
  i__const_desktop_61: desktop;
  i__const_tabMobile_62: tabMobile;
  i__const_mobile_63: mobile;
  i__const_tablet_64: tablet;
  i__const_notxs_65: notxs;
  i__const_cloud_66: cloud;
  i__const_primary_67: primary;
  i__const_sliver_68: sliver;
  i__const_grey_69: grey;
}
:export {
  desktop: i__const_desktop_61;
  tabMobile: i__const_tabMobile_62;
  mobile: i__const_mobile_63;
  tablet: i__const_tablet_64;
  notxs: i__const_notxs_65;
  cloud: i__const_cloud_66;
  primary: i__const_primary_67;
  sliver: i__const_sliver_68;
  grey: i__const_grey_69;
}
.card {
    position: relative;
}
@-webkit-keyframes clickMe {
    0% {
        -webkit-transform: rotateZ(0deg) translateY(0px);
                transform: rotateZ(0deg) translateY(0px);
    }
    30% {
        -webkit-transform: rotateZ(50deg);
                transform: rotateZ(50deg);
    }
    60% {
        -webkit-transform: rotateZ(-50deg);
                transform: rotateZ(-50deg);
    }
    100% {
        -webkit-transform: rotateZ(0deg) translateY(0px);
                transform: rotateZ(0deg) translateY(0px);
    }
}
@keyframes clickMe {
    0% {
        -webkit-transform: rotateZ(0deg) translateY(0px);
                transform: rotateZ(0deg) translateY(0px);
    }
    30% {
        -webkit-transform: rotateZ(50deg);
                transform: rotateZ(50deg);
    }
    60% {
        -webkit-transform: rotateZ(-50deg);
                transform: rotateZ(-50deg);
    }
    100% {
        -webkit-transform: rotateZ(0deg) translateY(0px);
                transform: rotateZ(0deg) translateY(0px);
    }
}

@media i__const_notxs_65 {
    .imgWrapper {
        overflow: hidden;
    }
}

@media i__const_mobile_63 {
    .absMobBasket {
        position: absolute;
        right: 10px;
        top: 8px;
    }
    .itemText {
        padding: 0 10px;
    }
    .basketContainer {
        composes: absMobBasket;
        color: i__const_sliver_68;
    }
    .addedBasketContainer {
        composes: absMobBasket;
        color: i__const_primary_67;
    }
}

@media i__const_tablet_64 {
    .absTabBasket {
        position: absolute;
        right: 0px;
        top: 8px;
    }
    .basketContainer {
        composes: absTabBasket;
        color: i__const_sliver_68;
    }
    .addedBasketContainer {
        composes: absTabBasket;
        color: i__const_primary_67;
    }
}

@media i__const_tabMobile_62 {
    .itemImg {
        padding-top: 70%;
        width: 100%;
        background-size: cover;
        background-position: center;
    }
    .itemText {
        position: relative;
    }
    .title {
        padding: 8px 0;
    }
    .time {
        border-radius: 5px;
        display: inline-block;
        color: i__const_grey_69;
        border: 1px solid i__const_grey_69;
        padding: 3px;
        font-size: 14px;
    }
}

@media i__const_desktop_61 {
    .itemImg {
        padding-top: 90%;
        width: 100%;
        background-size: cover;
        background-position: center;
    }
    .absDeskBasket {
        top: 5px;
        right: 5px;
        padding: 10px;
    }
    .insideWrapper {
        opacity: 0;
        position: absolute;
    }
    .shadow {
        composes: insideWrapper;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: -webkit-gradient( linear, left top, left bottom, color-stop(0, rgba(0, 0, 0, 0.2)), color-stop(30%, transparent), color-stop(70%, transparent), to(rgba(0, 0, 0, 0.3)));
        background: -webkit-linear-gradient( top, rgba(0, 0, 0, 0.2) 0, transparent 30%, transparent 70%, rgba(0, 0, 0, 0.3));
        background: -o-linear-gradient( top, rgba(0, 0, 0, 0.2) 0, transparent 30%, transparent 70%, rgba(0, 0, 0, 0.3));
        background: linear-gradient( 180deg, rgba(0, 0, 0, 0.2) 0, transparent 30%, transparent 70%, rgba(0, 0, 0, 0.3));
    }
    .title {
        composes: insideWrapper;
        bottom: 5px;
        padding: 10px;
        color: i__const_cloud_66;
    }
    .time {
        composes: insideWrapper;
        top: 5px;
        padding: 10px;
        color: i__const_cloud_66;
    }
    .basketContainer {
        composes: insideWrapper;
        composes: absDeskBasket;
        top: 5px;
        right: 5px;
        padding: 10px;
        color: #ffffff;
        cursor: pointer;
    }
    .addedBasketContainer {
        composes: insideWrapper;
        composes: absDeskBasket;
        color: i__const_primary_67;
        cursor: pointer;
    }
    .basketContainer:hover {
        -webkit-animation-name: clickMe;
                animation-name: clickMe;
        -webkit-animation-duration: 0.4s;
                animation-duration: 0.4s;
        -webkit-animation-iteration-count: 1;
                animation-iteration-count: 1;
    }
    .addedBasketContainer:hover {
        -webkit-animation-name: clickMe;
                animation-name: clickMe;
        -webkit-animation-duration: 1s;
                animation-duration: 1s;
        -webkit-animation-iteration-count: 1;
                animation-iteration-count: 1;
    }
    .card:hover .insideWrapper {
        opacity: 1;
    }
    .card:hover .itemImg {
        -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
                transform: scale(1.1);
        -webkit-transition: 0.5s ease-out;
        -o-transition: 0.5s ease-out;
        transition: 0.5s ease-out;
    }
}


