:import("../shared/variable.css") {
  i__const_cloud_42: cloud;
  i__const_sliver_43: sliver;
}

:export {
  cloud: i__const_cloud_42;
  sliver: i__const_sliver_43;
}

.d0 {
    width: 100%;
    border-radius: 4px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    background: i__const_cloud_42;
    border: 1px solid i__const_cloud_42;
}

.d0:focus-within {
    background-color: #fff;
    border: 1px solid i__const_sliver_43;
}

.d0 button {
    background: transparent;
    border: 0;
    outline: 0;
}

.d0 input {
    background: transparent;
    border: 0;
    outline: 0;
    width: 100%;
}

.search {
    color: i__const_sliver_43;
    font-size: 16px;
    composes: search from "../shared/icon.css";
    padding-left: 12px;
}

.cross {
    color: i__const_sliver_43;
    font-size: 14px;
    composes: cross from "../shared/icon.css";
    padding-right: 10px;
}



