:import("../shared/variable.css") {
  i__const_primary_38: primary;
  i__const_sliver_39: sliver;
  i__const_deskTab_40: deskTab;
  i__const_mobile_41: mobile;
}

:export {
  primary: i__const_primary_38;
  sliver: i__const_sliver_39;
  deskTab: i__const_deskTab_40;
  mobile: i__const_mobile_41;
}

@media i__const_deskTab_40 {
    .nav {
        display: -ms-flexbox;
        display: flex;
        padding: 16px 24px;
    }
    .nav > div,
    .nav > a {
        margin: 0 16px;
    }
}

@media i__const_mobile_41 {
    .nav {
        display: -ms-flexbox;
        display: flex;
        padding: 10px 0;
    }
    .nav > div,
    .nav > a {
        margin: 0 8px;
    }
}

.nav > div,
.nav > a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}

.logo {
    composes: logo from "../shared/icon.css";
    font-size: 36px;
    color: i__const_primary_38;
}

.chef {
    composes: chef from "../shared/icon.css";
    color: i__const_sliver_39;
}

.basketIcon {
    composes: basket from "../shared/icon.css";
    color: i__const_sliver_39;
}

.basket {
    position: relative;
}

.basket > .quantity {
    position: absolute;
    background: i__const_primary_38;
    color: #fff;
    border-radius: 50%;
    top: -12px;
    right: -12px;
    position: absolute;
    height: 24px;
    width: 24px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 8px;
    padding-top: 3px;
    font-size: 14px;
}

.logoLink {
    text-decoration: none;
}



