:import("../shared/variable.css") {
  i__const_sliver_44: sliver;
  i__const_grey_45: grey;
  i__const_cloud_46: cloud;
  i__const_dark_47: dark;
  i__const_primary_48: primary;
  i__const_mobile_49: mobile;
  i__const_deskTab_50: deskTab;
}

:export {
  sliver: i__const_sliver_44;
  grey: i__const_grey_45;
  cloud: i__const_cloud_46;
  dark: i__const_dark_47;
  primary: i__const_primary_48;
  mobile: i__const_mobile_49;
  deskTab: i__const_deskTab_50;
}

@media i__const_deskTab_50 {
    .detailDiv {
        display: -ms-flexbox;
        display: flex;
        margin: 0 20px;
    }
    .stepsDiv {
        -ms-flex-order: 1;
            order: 1;
        width: 65%;
        padding: 30px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
    .list {
        -ms-flex-order: 2;
            order: 2;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 35%;
        padding: 30px;
    }
}

@media i__const_mobile_49 {
    .detailDiv {
        margin: 0 10px;
    }
    .list {
        margin-bottom: 20px;
    }
}

.stepItem {
    margin-bottom: 20px;
}

.list ul {
    list-style: none;
    padding: 0;
}

.list li {
    cursor: pointer;
    line-height: 30px;
}

.titleDiv {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.title {
    margin-bottom: 10px;
    font-size: 22px;
    font-weight: 600;
}

.separator {
    width: calc(100% - 70px);
    height: 1px;
    background-color: i__const_sliver_44;
}



