:import("../shared/variable.css") {
  i__const_sliver_51: sliver;
  i__const_cloud_52: cloud;
  i__const_primary_53: primary;
  i__const_mobile_54: mobile;
  i__const_deskTab_55: deskTab;
}

:export {
  sliver: i__const_sliver_51;
  cloud: i__const_cloud_52;
  primary: i__const_primary_53;
  mobile: i__const_mobile_54;
  deskTab: i__const_deskTab_55;
}

@media i__const_mobile_54 {
    .intro {
        padding: 14px 10px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
    .img {
        padding-bottom: 70%;
    }
    .titleDiv {
        padding: 5px 10px;
    }
    .tag {
        padding: 5px 10px;
        border: solid i__const_sliver_51 1px;
    }
}

@media i__const_deskTab_55 {
    .introDiv {
        position: relative;
        display: -ms-flexbox;
        display: flex;
        margin: 0 20px;
        border-radius: 8px;
        border: 1px solid i__const_sliver_51;
        overflow: hidden;
    }
    .titleDiv {
        border-bottom: 1px solid i__const_sliver_51;
        width: calc(35% - 48px);
        position: absolute;
        left: calc(65% + 24px);
        height: 80px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
    }
    .intro {
        margin-top: 80px;
        width: 35%;
        padding: 14px 24px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
    .img {
        width: 65%;
        padding-bottom: 50%;
    }
    .tag {
        padding: 10px 15px;
        border: i__const_cloud_52 solid 1px;
    }
    .tag:hover {
        border: grey solid 1px;
    }
}

.img {
    background-size: cover;
    background-position: center;
}

.number {
    color: i__const_sliver_51;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 15px;
}

.tag {
    margin-top: 15px;
    border-radius: 3px;
    margin-right: 10px;
    display: inline-block;
}

.number>span {
    padding-right: 10px;
}

.number>span:last-of-type {
    margin-left: auto;
}

.basket {
    composes: basket from "../shared/icon.css";
    cursor: pointer;
}

.addedBasket {
    composes: basket;
    color: i__const_primary_53;
}


